<template>
  <div class="city">
    <city-header></city-header>
    <city-search :cities='cities'></city-search>
    <city-list :letter='letter' :cities='cities' :hotCities='hotCities'></city-list>
    <city-right-button :cities='cities' @handleClickMenu='handleClickMenu'></city-right-button>
  </div>
</template>

<script>
import axios from 'axios'
import CityHeader from './components/Header'
import CitySearch from './components/Search'
import CityList from './components/List'
import CityRightButton from './components/RightButton'
export default {
  name: 'City',
  data () {
    return {
      cities: {},
      hotCities: [],
      letter: ''
    }
  },
  methods: {
    getCityInfo () {
      axios.get('/api/city.json')
        .then(this.getCityInfoSucc)
    },
    getCityInfoSucc (res) {
      if (res.data.ret && res.data.data) {
        this.cities = res.data.data.cities
        this.hotCities = res.data.data.hotCities
      }
    },
    handleClickMenu (letter) {
      //  右侧菜单按钮点击向city组件传递来的的字母值
      this.letter = letter
    }
  },
  mounted () {
    this.getCityInfo()
  },
  components: {
    CityHeader,
    CitySearch,
    CityList,
    CityRightButton
  }
}
</script>

<style lang="less" scoped>
</style>



