<template>
  <div class="week">
    <div class="title">周末游</div>
    <ul class="weekList" v-if='showWeek'>
      <li v-for="item in list" :key='item.id'>
        <div class="left-img">
          <img :src="item.imgUrl" alt="">
        </div>
        <div class="right-desc">
          <div class="title">{{ item.title }}</div>
          <p class="desc">{{ item.desc }}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    list: Array
  },
  computed: {
    showWeek () {
      return this.list.length
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/assets/styles/mixins.less';
.week{
  margin-top: 10px;
  & > .title{
    padding: .1rem;
    line-height: .34rem;
    font-size: .22rem;
    font-weight: bold;
    color: #333;
    background: #ddd;
  }
  .weekList{
    li{
      margin-bottom: .1rem;
      .left-img{
        overflow: hidden;
        height: 0;
        padding-bottom: 62.4%;
        background: #ddd;
        img{
          width: 100%;
          vertical-align: top;
        }
      }
      .right-desc{
        padding: 0 .1rem;
        .title{
          width: 100%;
          font-size:.22rem;
          color: #000;
          .ellipsis();
        }
        .desc{
          font-size: .22rem;
          color: #ccc;
          .ellipsis();
        }
      }
    }
  }
}
</style>



