<template>
  <div class="gallary-wrapper" @click="handleClickClose">
    <div class="img-wrapper">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(item, index) in imgs" :key="index">
          <img :src='item'/>
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-scrollbar" slot="scrollbar"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  props: {
    imgs: Array,
    default () {
      return []
    }
  },
  data () {
    return {
      swiperOption: {
        pagination: '.swiper-pagination',
        paginationType: 'fraction',
        //  重新初始化 swiper
        observeParents: true,
        observer: true
      }
    }
  },
  methods: {
    handleClickClose () {
      this.$emit('close')
    }
  }
}
</script>

<style lang="less" scoped>
  .gallary-wrapper /deep/ .swiper-pagination{
    color: #fff;
    font-size: .32rem;
    font-weight: bold;
  }
  .gallary-wrapper /deep/ .swiper-container{
    overflow: inherit;
  }
  .gallary-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #000;
    z-index: 99;
    .img-wrapper{
      // overflow: hidden;
      height: 0;
      padding-bottom: 100%;
      img{
        width: 100%;
      }
      .swiper-pagination {
        bottom: -1rem;
      }
    }
  }
</style>



