<template>
  <div class="detail">
    <!-- header区块 -->
    <detail-header></detail-header>
    <!-- banner区块 -->
    <detail-banner :bannerImgs="bannerImgs"
                   :gallaryImgs="gallaryImgs"
                   :sightName="sightName"
    ></detail-banner>
    <!-- list 区域 -->
    <detail-list :lists = "lists"></detail-list>
    <div class="main"></div>
  </div>
</template>

<script>
import axios from 'axios'
import DetailHeader from './components/Header'
import DetailBanner from './components/Banner'
import DetailList from './components/List'
export default {
  name: 'Detail',
  data () {
    return {
      sightName: '',
      bannerImgs: '',
      gallaryImgs: [],
      lists: []/* ,
      lastDetailId: 0 */
    }
  },
  methods: {
    getDetailData () {
      axios.get('/api/detail.json', {
        params: {
          id: this.$route.params.id
        }
      }).then(this.getDetailDataSucc)
      this.lastDetailId = this.$route.params.id
    },
    getDetailDataSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        let data = res.data
        this.lists = data.categoryList
        this.sightName = data.sightName
        this.bannerImgs = data.bannerImgs
        this.gallaryImgs = data.gallaryImgs
      }
    }
  },
  mounted () {
    this.getDetailData()
  },
  components: {
    DetailHeader,
    DetailBanner,
    DetailList
  }/* ,
  activated () {
    if (this.lastDetailId !== this.$route.params.id) {
      this.getDetailData()
    }
  } */
}
</script>

<style lang="less" scoped>
  .detail{
    .main{
      height: 30rem;
    }
  }
</style>



