<template>
  <ul class="right-button">
    <li
      @click='handleClickMenu(item)'
      @touchstart='handleTouchStart'
      @touchmove='handleTouchMove'
      @touchend='handleTouchEnd'
      class="button"
      v-for='item in letters'
      :key='item'
      :ref='item'
    >
      {{ item }}
    </li>
  </ul>
</template>

<script>
export default {
  props: {
    cities: Object
  },
  data () {
    return {
      isMove: false,
      //  初始A距离顶部距离
      startY: 0,
      //  初始定时器作touchmove性能节流
      timer: null
    }
  },
  methods: {
    handleClickMenu (key) {
      this.$emit('handleClickMenu', key)
    },
    handleTouchStart () {
      this.isMove = true
    },
    handleTouchMove (e) {
      if (this.isMove) {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          //  就算当前滑动最后距离顶部距离
          let touchY = e.touches[0].clientY - 81
          //  计算索引
          let index = Math.floor((touchY - this.startY) / 20)
          //  子组件传递值
          if (index >= 0 && this.letters.length) {
            this.$emit('handleClickMenu', this.letters[index])
          }
        })
      }
    },
    handleTouchEnd () {
      this.isMove = false
    }
  },
  computed: {
    letters () {
      let letters = []
      for (let key in this.cities) {
        letters.push(key)
      }
      return letters
    }
  },
  updated () {
    //  当页面数据发生变化的时候, 触发该钩子函数, 初始A所在的距离顶部高度.
    this.startY = this.$refs['A'][0].offsetTop
  }
}
</script>

<style lang="less" scoped>
@import '~@/assets/styles/common.less';
  .right-button{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    right: 0;
    width: .4rem;
    height: calc(100% - 1.62rem);
    .button{
      margin: .1rem 0;
      text-align: center;
      color: @bgColor;
    }
  }
</style>



