<template>
  <div class="menu">
    <swiper :options="swiperOption">
      <swiper-slide v-for='items in pages' :key="items.id">
        <ul>
          <li v-for="item in items" :key='item.id'>
            <a :href="item.link_url" class="icon-img">
              <img :src="item.icon_url" alt="">
            </a>
            <div class="icon-desc">{{ item.icon_desc }}</div>
          </li>
        </ul>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/assets/styles/mixins.less';
  .menu{
    ul{
      display: flex;
      flex-wrap: wrap;
      li{
        width: 25%;
        text-align: center;
        .icon-img{
          display: block;
          margin-top: 10%;
          width:100%;
          img{
            width: 64%;
            vertical-align: top;
          }
        }
        .icon-desc{
          font-size: .22rem;
          .ellipsis()
        }
      }
    }
  }
</style>



