<template>
  <div class="header">
    <div class="left"><span class="iconfont">&#xe624;</span></div>
    <div class="inp"><span class="iconfont">&#xe603;</span>输入城市/景点/游玩/主题</div>
    <router-link tag="div" class="right" to="/City">
      {{ this.city }}
      <span class="iconfont">&#xe8d2;</span>
    </router-link>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="less" scoped>
@import '~@/assets/styles/common.less';
.header{
  display: flex;
  height: @headerLineHeight;
  color: #fff;
  background: @bgColor;
  .left{
    float: left;
    width: .86rem;
    line-height: .86rem;
    text-align: center;
    font-size: .26rem;
    span{
      color: #fff;
      font-weight: 600;
      font-size: .32rem;
    }
  }
  .inp{
    flex:1;
    margin: .12rem 0 0 .06rem;
    padding: 0rem .12rem;
    line-height: 0.64rem;
    height: 0.64rem;
    color: #ccc;
    font-size: .26rem;
    background: #fff;
    border-radius: 0.1rem;
    span{
      margin-right: .12rem;
    }
  }
  .right{
    float: right;
    padding: 0 .1rem;
    line-height: .86rem;
    text-align: center;
    min-width: 1.24rem;
    font-size: .26rem;
    color: #fff;
    span{
      margin-left: .06rem;
      font-size: .24rem;
    }
  }
}
</style>



