<template>
  <div class="detail-banner">
    <div class="banner-wrapper" @click='handleClickShowGallary'>
      <div class="banner-img">
        <img :src="bannerImgs" alt="">
      </div>
      <div class="banner-info">
        <div class="banner-title">{{ sightName }}</div>
        <div class="banner-picnum">
          <span class="iconfont">&#xe692;</span>
          <span class="num">{{ this.gallaryImgs.length }}</span>
        </div>
      </div>
    </div>
    <fade-animation>
      <detail-gallary @close='handleClickClose'
                      v-show="showgallary"
                      :imgs='gallaryImgs'
      ></detail-gallary>
    </fade-animation>
  </div>
</template>

<script>
import DetailGallary from 'common/gallary/Gallary'
import FadeAnimation from 'common/fade/FadeAnimation'
export default {
  props: {
    sightName: String,
    bannerImgs: String,
    gallaryImgs: Array
  },
  data () {
    return {
      showgallary: false
    }
  },
  methods: {
    handleClickShowGallary () {
      this.showgallary = true
    },
    handleClickClose () {
      this.showgallary = false
    }
  },
  components: {
    DetailGallary,
    FadeAnimation
  }
}
</script>

<style lang="less" scoped>
.detail-banner{
  position: relative;
  .banner-wrapper{
    position: relative;
    .banner-img{
      overflow: hidden;
      height: 0;
      padding-bottom: 65.625%;
      background: #ccc;
      img{
        width: 100%;
      }
    }
    .banner-info{
      display: flex;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      background-image: linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
      .banner-title{
        flex: 1;
        padding: 0 .2rem;
        height: .64rem;
        line-height: .64rem;
        font-size: .28rem;
        color: #fff;
      }
      .banner-picnum{
        margin: .12rem .12rem 0 0;
        padding: 0 .2rem;
        height: .42rem;
        line-height: .42rem;
        font-size: .28rem;
        color: #fff;
        background: rgba(0, 0, 0, .8);
        border-radius: .1rem;
        .num{
          font-weight: bold;
        }
      }
    }
  }
}
</style>



