<template>
  <div class="detail-header">
    <router-link tag='div' to="/" class="header-abs" v-show="showAbs">
      <span class="iconfont">&#xe624;</span>
    </router-link>
    <div :style="headerStyle" class="header-fixed" v-show="!showAbs">
      详情
      <router-link to="/">
        <div class="iconfont goback">&#xe624;</div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showAbs: true,
      headerStyle: {
        'opacity': 0
      }
    }
  },
  methods: {
    handleScroll () {
      let top = document.documentElement.scrollTop
      if (top > 60) {
        this.showAbs = false
        //  实现头部 opacity 的渐入
        this.headerStyle.opacity = (top / 140 > 1 ? 1 : top / 140)
      } else {
        this.showAbs = true
      }
    }
  },
  activated () {
    //  滚动事件的监听
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated () {
    //  滚动事件监听的去除
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="less" scoped>
@import '~@/assets/styles/common';
  .detail-header{
    position: absolute;
    z-index: 2;
    .header-abs{
      margin: .2rem;
      width: .84rem;
      height: .84rem;
      line-height: .84rem;
      text-align: center;
      border-radius: 50%;
      background: rgba(0, 0, 0, .8);
      span{
        font-size: .32rem;
        color: #fff;
      }
    }
    .header-fixed{
      position: fixed;
      width: 100%;
      height: @headerLineHeight;
      line-height: @headerLineHeight;
      text-align: center;
      font-size: .36rem;
      color: #fff;
      background: @bgColor;
      .goback{
        position: absolute;
        top: 0;
        left: 0;
        width: .64rem;
        height: @headerLineHeight;
        line-height: @headerLineHeight;
        font-size: .4rem;
        color: #fff;
      }
    }
  }
</style>



