<template>
  <div class="recommend">
    <div class="title">热销推荐</div>
    <ul class="recommendList" v-if="showRecommend">
      <router-link tag="li" v-for="item in list" :key='item.id' :to="'/Detail/' + item.id">
        <div class="left-img">
          <img :src="item.imgUrl" alt="">
        </div>
        <div class="right-desc">
          <div class="title">{{ item.title }}</div>
          <p class="desc">{{ item.desc }}</p>
          <span class="seedetail">查看详情</span>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    list: Array
  },
  computed: {
    showRecommend () {
      return this.list.length
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/assets/styles/mixins.less';
.recommend{
  margin-top: 10px;
  & > .title{
    padding: .1rem;
    line-height: .34rem;
    font-size: .22rem;
    font-weight: bold;
    color: #333;
    background: #ddd;
  }
  .recommendList{
    li{
      display: flex;
      padding: .1rem;
      .left-img{
        img{
          vertical-align: top;
          width: 1.1rem;
          height: 1.2rem;
        }
      }
      .right-desc{
        flex: 1;
        padding: 0 .1rem;
        min-width: 0;
        .title{
          width: 100%;
          font-size:.22rem;
          color: #333;
          .ellipsis();
        }
        .desc{
          font-size: .22rem;
          color: #ddd;
          .ellipsis();
        }
        .seedetail{
          display: inline-block;
          padding: .05rem .1rem;
          margin-top: .1rem;
          vertical-align: top;
          font-size: .2rem;
          color: #fff;
          background: #f60;
          border-radius: .05rem;
        }
      }
    }
  }
}
</style>



