<template>
  <div class="search-wrapper">
    <div class="search">
      <input v-model="keyword" class="search-input" placeholder="请输入城市名/拼音" />
    </div>
    <div class="list-wrapper" ref="list-wrapper" v-show="keyword">
      <ul class="list">
        <li class="item" v-for="item in keyWordMessage" :key="item.id" @click="handleClickChangeCity(item.name)">{{ item.name }}</li>
        <li class="item" v-show="noHasMessage">未查询到数据</li>
      </ul>
    </div>
  </div>
</template>

<script>
import BScorll from 'better-scroll'
import {mapMutations} from 'vuex'
export default {
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      keyWordMessage: [],
      //  作数据节流使用
      timer: null
    }
  },
  methods: {
    handleClickChangeCity (city) {
      // this.$store.commit('changeCity', city)
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeCity'])
  },
  mounted () {
    this.scroll = new BScorll(this.$refs['list-wrapper'])
  },
  computed: {
    noHasMessage () {
      return !this.keyWordMessage.length
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setTimeout(() => {
        let cities = this.cities
        let keyword = this.keyword
        let keywordmsg = []
        for (let key in cities) {
          cities[key].forEach((item, index) => {
            if (item.name.indexOf(keyword) > -1 || new RegExp(keyword, 'i').test(item.spell)) {
              keywordmsg.push(item)
            }
          })
        }
        this.keyWordMessage = keywordmsg
      }, 300)
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/assets/styles/common.less';
.search-wrapper{
  overflow: hidden;
  .search{
    padding: .1rem;
    height: .76rem;
    background: @bgColor;
    .search-input{
      padding: 0rem .1rem;
      width: 100%;
      height: .58rem;
      text-align: center;
      line-height: .58rem;
      font-size: .24rem;
      color: #333;
      border-radius: .06rem;
      box-sizing: border-box;
    }
  }
  .list-wrapper{
    overflow: hidden;
    position: absolute;
    width: 100%;
    top: 1.62rem;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    padding: .1rem;
    background: #fff;
    .list{
      .item{
        line-height: .65rem;
        color: #333;
        .border-1px(#ddd);
      }
    }
  }
}
</style>



