<template>
  <div class="detail-list">
    <div class="list" v-for="(list, index) in lists" :key="index">
      <div class="title border-1px">
        <span class="iconfont">&#xe643;</span>
        {{ list.title }}
      </div>
      <div class="cont">{{ list.cont }}</div>
      <!-- 递归组件的使用, 根据组件内部设定 name 来确定调用组件对象 -->
      <div class="list-children" v-if='list.children'>
        <detail-list :lists='list.children'></detail-list>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailList',
  props: {
    lists: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {

    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/assets/styles/common.less';
.detail-list{
  .list{
    margin-top: -.13rem;
    padding: .06rem;
    .title{
      padding: .1rem 0;
      text-align: center;
      font-size: .32rem;
      font-weight: bold;
      .border-topbottom(#333);
    }
    .cont{
      text-indent: .2rem;
      min-height: 1rem;
    }
    .list-children{
      padding: .12rem;
      .title{
        font-size: .24rem;
        font-weight: 100;
        .border-topbottom(rgba(0, 0, 0, 0));
        .border-1px(#ddd);
      }
    }
  }
}
</style>



