<template>
  <div class="list" ref='list-wrapper'>
    <div>
      <div class="items">
        <div class="items-title border-1px">当前城市</div>
        <div class="items-cont">
          <div class="item-wrapper">
            <div class="item">{{ this.currentCity }}</div>
          </div>
        </div>
      </div>
      <div class="items">
        <div class="items-title border-1px">热门城市</div>
        <div class="items-cont">
          <div class="item-wrapper" v-for="item in hotCities" :key='item.id' @click='handleClickChangeCity(item.name)'>
            <div class="item">{{ item.name }}</div>
          </div>
        </div>
      </div>
      <div class="items" v-for="(item, key) in cities" :key="key" ref='key'>
        <div class="items-title border-1px">{{ key }}</div>
        <div class="items-list" v-for="itemInner in item" :key="itemInner.id" @click='handleClickChangeCity(itemInner.name)'>
          <div class="item">{{ itemInner.name }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapState, mapMutations} from 'vuex'
export default {
  props: {
    cities: Object,
    hotCities: Array,
    letter: String
  },
  data () {
    return {}
  },
  methods: {
    handleClickChangeCity (city) {
      this.changeCity(city)
      this.$router.push('/')
    },
    ...mapMutations({
      changeCity: 'changeCity'
    })
  },
  mounted () {
    this.scroll = new BScroll(this.$refs['list-wrapper'])
  },
  computed: {
    //  计算所有字母组成的数组 ['A', 'B', 'C', ...]
    letters () {
      let letters = []
      for (const key in this.cities) {
        letters.push(key)
      }
      return letters
    },
    ...mapState({
      currentCity: 'city'
    })
  },
  watch: {
    letter () {
      let index = this.letters.indexOf(this.letter)
      //  better-scroll 插件的一个方法, scrollToElement 滚动到指定 dom 节点位置
      this.scroll.scrollToElement(this.$refs['key'][index])
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/assets/styles/common.less';
.list{
  position: absolute;
  top: 1.62rem;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  .items{
    .items-title{
      padding: .08rem .1rem;
      font-size: .16rem;
      font-weight: bold;
      background: #ddd;
      .border-topbottom(#ccc);
    }
    .items-cont{
      overflow: hidden;
      padding-left: .1rem;
      padding-right: .6rem;
      .item-wrapper{
        float: left;
        width: 33.33%;
        .item{
          margin: .1rem;
          padding: .1rem 0;
          text-align: center;
          border: .02rem solid #ccc;
          border-radius: .06rem;
        }
      }
    }
    .items-list{
      padding: .1rem .6rem 0 .1rem;
      .item{
        padding: .1rem;
        .border-1px(#eee);
      }
    }
  }
}
</style>



