<template>
  <div class="home_cont">
    <!-- 头部 -->
    <Header></Header>
    <!-- 轮播 -->
    <Swiper :list='swiperList'></Swiper>
    <!-- 菜单 -->
    <Menu :list='menuList'></Menu>
    <!-- 推荐 -->
    <Recommend :list='recommendList'></Recommend>
    <!-- 周末游 -->
    <Week :list='WeekList'></Week>
  </div>
</template>

<script>
import axios from 'axios'
import Header from './components/Header'
import Swiper from './components/Swiper'
import Menu from './components/Menu'
import Recommend from './components/Recommend'
import Week from './components/Week'
import { mapState } from 'vuex'
export default {
  name: 'Home',
  data () {
    return {
      swiperList: [],
      menuList: [],
      recommendList: [],
      WeekList: [],
      lastcity: ''
    }
  },
  methods: {
    getHomeInfo () {
      axios.get('/api/index.json?city=' + this.currentCity)
        .then(this.getHomeInfoSucc)
      this.lastcity = this.currentCity
    },
    getHomeInfoSucc (res) {
      if (res.data.ret) {
        let homeData = res.data.data
        this.city = homeData.city
        this.swiperList = homeData.swiperList
        this.menuList = homeData.menuList
        this.recommendList = homeData.recommendList
        this.WeekList = homeData.weekList
      }
    }
  },
  computed: {
    ...mapState({
      currentCity: 'city'
    })
  },
  mounted () {
    this.getHomeInfo()
  },
  activated () {
    if (this.lastcity !== this.currentCity) {
      this.getHomeInfo()
    }
  },
  components: {
    Header,
    Swiper,
    Menu,
    Recommend,
    Week
  }
}
</script>

<style lang="less" scoped>

</style>



